import * as React from 'react';
//import * as PropTypes from 'prop-types';
//import styles from './Header.module.css';
import { useIntl } from 'react-intl';
import { Index_posts_edges_node as Post } from '~pages/__generated__/Index';
import AuthorList from '~components/Sections/AuthorList/AuthorList';
import Link from '~components/utils/Link/Link';
import ImgSharp from '~components/utils/ImgSharp/ImgSharp';
import styles from './PostCard.module.scss';

const options = {
	year: 'numeric',
	month: 'short',
	day: 'numeric',
};

const PostCard: React.FC<{
	post: Post;
	i?: number;
	isHome?: boolean;
}> = ({ post, i, isHome }) => {
	const intl = useIntl();
	const isFeatured = post.frontmatter.featured;
	return (
		<article
			className={
				styles.postCard +
				` ` +
				((isHome && isFeatured && 0 === i % 6 && styles.postCardLarge) || ``)
			}
		>
			<Link
				className={styles.postCardImageLink}
				to={`/${post.frontmatter.category}/${post.fields.slug}`}
			>
				<ImgSharp
					className={styles.postCardImage}
					alt={post.frontmatter.title}
					/* 	sizes="(max-width: 1000px) 400px, 700px" */
					fluid={post.frontmatter.image.childImageSharp.fluid}
				/>
			</Link>
			<div className={styles.postCardContent}>
				<Link
					className={styles.postCardContentLink}
					to={`/${post.frontmatter.category}/${post.fields.slug}`}
				>
					<header className={styles.postCardHeader}>
						<div className={styles.postCardPrimaryTag}>
							{post.frontmatter.tags[0] !== undefined &&
								intl.formatMessage({
									id: `tags.${post.frontmatter.tags[0]}.title`,
								})}
						</div>
						<h2 className={styles.postCardTitle}>{post.frontmatter.title}</h2>
					</header>
					<section className={styles.postCardExcerpt}>
						<p>{post.excerpt}</p>
					</section>
				</Link>
				<footer className={styles.postCardMeta}>
					<AuthorList authors={post.frontmatter.authors} />

					<div className={styles.postCardBylineContent}>
						<span>
							{/* <Link to={`/authors/${post.frontmatter.authors[0].id}/`}> */}
							{intl.formatMessage({
								id: `authors.${post.frontmatter.authors[0].id}.title`,
							})}
							{/* </Link> */}
						</span>
						<span className={styles.postCardBylineDate}>
							<time dateTime="2018-08-20">
								{intl.formatDate(post.frontmatter.date, options)}
							</time>{' '}
							<span className="bull">â€¢</span>{' '}
							{intl.formatNumber(post.timeToRead)}{' '}
							{intl.formatMessage({ id: 'post.timeToRead' })}
						</span>
					</div>
				</footer>
			</div>
		</article>
	);
};

export default PostCard;
