import * as React from 'react';
//import * as PropTypes from 'prop-types';
//import Link from '~components/utils/Link/Link';
import Img from '~components/utils/ImgSharp/ImgSharp';
import HoverOnAvatar from './HoverEffect/HoverOnAvatar';
import AvatarIcon from '~assets/icons/avatar-icon';
import { useIntl } from 'react-intl';

import styles from './AuthorList.module.scss';

import { Post_post_frontmatter_authors as Authors } from '~templates/__generated__/Post';
import { Author_author } from '~templates/__generated__/Author';

const AuthorList: React.FC<{
	authors?: Authors;
	isPost?: boolean;
}> = ({ authors, isPost }) => {
	const intl = useIntl();
	return (
		<ul className={styles.authorList}>
			{authors.map((author: Author_author, i: React.Key) => (
				<HoverOnAvatar key={i} activeClass={styles.hovered}>
					{(hover: {
						anchorRef: React.LegacyRef<HTMLLIElement>;
						state: { currentClass: string };
					}) => (
						<li key={i} ref={hover.anchorRef} className={styles.authorListItem}>
							{!isPost && (
								<div className={styles.authorNameTooptip}>
									{intl.formatMessage({ id: `authors.${author.id}.title` })}
								</div>
							)}
							{isPost && (
								<div
									className={`${styles.authorCard} ${hover.state.currentClass}`}
								>
									{author.avatar ? (
										<Img
											className={styles.authorProfileImage}
											fixed={author.avatar.childImageSharp.fixed}
											fluid={author.avatar.childImageSharp.fluid}
											alt={author.title}
										/>
									) : (
										<div className={styles.authorProfileImage}>
											<AvatarIcon />
										</div>
									)}
									<div className={styles.authorInfo}>
										{author.bio ? (
											<div className={styles.bio}>
												<h2>
													{intl.formatMessage({
														id: `authors.${author.id}.title`,
													})}
												</h2>
												<p>
													{intl.formatMessage({
														id: `authors.${author.id}.bio`,
													})}
												</p>
												{/*	<p>
												 <Link to={`/authors/${author.id}/`}>  More
													posts
												 </Link> by{' '}
													{intl.formatMessage({
														id: `authors.${author.id}.title`,
													})}
													.
												</p>*/}
											</div>
										) : (
											<>
												<h2>
													{intl.formatMessage({
														id: `authors.${author.id}.title`,
													})}
												</h2>
												{/* 			{									<p>
													Read <Link to={`/authors/${author.id}/`}>more
													posts</Link> by this author.
												</p>} */}
											</>
										)}
									</div>
								</div>
							)}
							{author.avatar ? (
								/* 			<Link
									to={`/authors/${author.id}/`}
									className={isPost ? styles.authorAvatar : styles.staticAvatar}
								> */
								<span
									className={`${
										isPost ? styles.authorAvatar : styles.staticAvatar
									} ${styles.authorProfileImage}`}
								>
									<Img
										className={styles.authorProfileImage}
										fixed={author.avatar.childImageSharp.fixed}
										fluid={author.avatar.childImageSharp.fluid}
										alt={author.id}
									/>
								</span>
							) : (
								/* 		</Link> */
								/* 				<Link
									to={`/authors/${author.id}/`}
									className={`${
										isPost ? styles.authorAvatar : styles.staticAvatar
									} ${styles.authorProfileImage}`}
								> */
								<span
									className={`${
										isPost ? styles.authorAvatar : styles.staticAvatar
									} ${styles.authorProfileImage}`}
								>
									<AvatarIcon />
								</span>
								/* </Link> */
							)}
						</li>
					)}
				</HoverOnAvatar>
			))}
		</ul>
	);
};

/* AuthorList.propTypes = {
	authors: PropTypes.arrayOf(
		PropTypes.shape({
			name: PropTypes.string.isRequired,
			id: PropTypes.string.isRequired,
			avatar: PropTypes.shape({
				childImageSharp: {
					fixed: PropTypes.oneOfType([
						fixedObject,
						PropTypes.arrayOf(fixedObject),
					]),
					fluid: PropTypes.oneOfType([
						fluidObject,
						PropTypes.arrayOf(fluidObject),
					]),
				},
			}).isRequired,
			bio: PropTypes.string,
		}),
	).isRequired,
	isPost: PropTypes.bool,
}; */
export default AuthorList;
