import * as React from 'react';
import Img, { FixedObject, FluidObject } from 'gatsby-image';

const ImgSharp: React.FC<{
	className?: string;
	fixed?: FixedObject;
	fluid?: FluidObject;
	src?: string;
	alt?: string;
}> = ({ className, fixed, fluid, src, alt, ...props }) => (
	<>
		{fixed ? (
			<Img className={className} fixed={fixed} alt={alt} {...props} />
		) : fluid ? (
			<Img className={className} fluid={fluid} alt={alt} {...props} />
		) : (
			src && <img className={className} src={src} alt={alt} {...props} />
		)}
	</>
);
