/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import * as React from 'react';
//import * as PropTypes from 'prop-types';

interface HoverOnAvatarProps {
	anchorRef?: React.RefObject<HTMLDivElement>;
	activeClass?: string;
	hoverTimeout?: NodeJS.Timeout;
	children: (hover) => JSX.Element;
}

interface HoverOnAvatarState {
	currentClass?: string;
}
export default class HoverOnAvatar extends React.Component<
	HoverOnAvatarProps,
	HoverOnAvatarState
> {
	activeClass: string;
	hoverTimeout: NodeJS.Timeout;
	anchorRef: React.RefObject<HTMLDivElement>;

	constructor(
		props: Readonly<{
			anchorRef: React.RefObject<HTMLDivElement>;
			activeClass: string;
			hoverTimeout: NodeJS.Timeout;
			children: (hover) => JSX.Element;
		}>,
	) {
		super(props);
		this.anchorRef = React.createRef();
		(this.activeClass = this.props.activeClass),
			(this.hoverTimeout = null),
			(this.state = {});
	}

	componentDidMount() {
		this.anchorRef.current &&
			this.anchorRef.current.addEventListener(`mouseout`, this.onHoverOut, {
				passive: true,
			});
		this.anchorRef.current &&
			this.anchorRef.current.addEventListener(`mouseover`, this.onHoverIn, {
				passive: true,
			});
	}

	componentWillUnmount() {
		clearTimeout(this.hoverTimeout);
		this.anchorRef.current.removeEventListener(`mouseover`, this.onHoverIn);
		this.anchorRef.current.removeEventListener(`mouseout`, this.onHoverOut);
	}

	onHoverIn = () => {
		clearTimeout(this.hoverTimeout);
		this.setState({ currentClass: this.activeClass });
	};

	onHoverOut = () => {
		// eslint-disable-next-line @typescript-eslint/no-this-alias
		const self = this;
		this.hoverTimeout = setTimeout(function () {
			self.setState({ currentClass: `` });
		}, 200);
	};

	render() {
		return this.props.children(this);
	}
}
