import * as React from 'react';
import { graphql } from 'gatsby';
import { Author } from '~templates/__generated__/Author';
import Layout from '~components/Layout/Template/Layout';
import PostCard from '~components/Sections/PostCard/PostCard';
//import { AuthorHero } from '~components/Layout/Headers/Hero';

//import AuthorLayout from '~components/Layout/Templates/AuthorLayout/AuthorLayout';

const AuthorTemplate: React.FC<{ data: Author }> = ({ data }) => {
	//	const { author, posts } = data;
	//const postsNumber = posts.edges.length;
	return (
		<>
			<Layout template="author" data={data}>
				{/* 		<AuthorHero author={data.author} posts={data.posts} /> */}
				<div className="inner">
					<div className=" posts">
						<div className="post-feed">
							{data.posts.edges.map((post, i) => {
								return <PostCard post={post.node} i={i} key={i} />;
							})}
						</div>
					</div>
				</div>
			</Layout>
		</>
	);
};

export default AuthorTemplate;

export const AuthorTemplateQuery = graphql`
	query Author($id: String!, $locale: String!) {
		author: authorsYaml(id: { eq: $id }) {
			id
			bio
			location
			facebook
			twitter
			avatar {
				childImageSharp {
					fluid {
						...GatsbyImageSharpFluid_withWebp_tracedSVG
					}
				}
			}
			cover {
				childImageSharp {
					fluid(maxWidth: 1080, grayscale: true) {
						...GatsbyImageSharpFluid_withWebp_tracedSVG
					}
				}
			}
			id
		}
		posts: allMdx(
			filter: {
				fields: { locale: { eq: $locale } }
				frontmatter: { authors: { elemMatch: { id: { eq: $id } } } }
			}
		) {
			edges {
				node {
					frontmatter {
						featured
						date(formatString: "DD MMMM YYYY")
						title
						category
						tags
						image {
							childImageSharp {
								fluid {
									...GatsbyImageSharpFluid_withWebp_tracedSVG
								}
							}
						}
						authors {
							id
							avatar {
								childImageSharp {
									fluid(maxWidth: 250, maxHeight: 250) {
										...GatsbyImageSharpFluid_withWebp_tracedSVG
									}
								}
							}
						}
					}
					fields {
						slug
					}
					excerpt(pruneLength: 200, truncate: true)
					timeToRead
				}
			}
		}
	}
`;
